/**
 * Organization Types
 * Shared type definitions for organization functionality
 */

export type OrganizationType =
  | 'dao'
  | 'company'
  | 'nonprofit'
  | 'community'
  | 'cooperative'
  | 'foundation'
  | 'collective'
  | 'guild'
  | 'syndicate';

export type GovernanceModel =
  | 'hierarchical'
  | 'flat'
  | 'democratic'
  | 'consensus'
  | 'liquid_democracy'
  | 'quadratic_voting'
  | 'stake_weighted'
  | 'reputation_based';

export interface Organization {
  id: string;
  profile_id: string;
  name: string;
  slug: string;
  description?: string;
  avatar_url?: string;
  banner_url?: string;
  website_url?: string;
  type: OrganizationType;
  category?: string;
  tags: string[];
  governance_model: GovernanceModel;
  treasury_address?: string;
  treasury_balance: number;
  is_public: boolean;
  requires_approval: boolean;
  contact_info: Record<string, any>;
  settings: Record<string, any>;
  member_count: number;
  campaign_count: number;
  total_funding: number;
  trust_score: number;
  status: 'active' | 'inactive' | 'suspended' | 'dissolved';
  founded_at?: string;
  dissolved_at?: string;
  created_at: string;
  updated_at: string;
}

export interface OrganizationFormData {
  name: string;
  description?: string;
  website_url?: string;
  avatar_url?: string;
  banner_url?: string;
  type: OrganizationType;
  category?: string;
  tags: string[];
  governance_model: GovernanceModel;
  treasury_address?: string;
  is_public: boolean;
  requires_approval: boolean;
  contact_info?: Record<string, any>;
  settings?: Record<string, any>;
}

export interface OrganizationSearchParams {
  q?: string;
  type?: OrganizationType;
  category?: string;
  page?: number;
  limit?: number;
  sort?: 'name' | 'created_at' | 'trust_score';
  order?: 'asc' | 'desc';
}

export interface Membership {
  id: string;
  organization_id: string;
  profile_id: string;
  role: 'owner' | 'admin' | 'moderator' | 'member' | 'guest';
  status: 'active' | 'inactive' | 'suspended' | 'banned';
  title?: string;
  bio?: string;
  joined_at: string;
  last_active_at: string;
  permissions: Record<string, boolean>;
  created_at: string;
  updated_at: string;
}

export interface OrganizationProposal {
  id: string;
  organization_id: string;
  proposer_id: string;
  title: string;
  description: string;
  proposal_type: 'governance' | 'funding' | 'membership' | 'policy' | 'other';
  voting_method: 'simple_majority' | 'super_majority' | 'consensus' | 'quadratic';
  voting_deadline?: string;
  minimum_quorum: number;
  status: 'pending' | 'active' | 'passed' | 'failed' | 'cancelled';
  votes_for: number;
  votes_against: number;
  votes_abstain: number;
  metadata: Record<string, any>;
  created_at: string;
  updated_at: string;
}

export interface OrganizationVote {
  id: string;
  proposal_id: string;
  voter_id: string;
  vote_type: 'for' | 'against' | 'abstain';
  voting_power: number;
  vote_weight: number;
  metadata: Record<string, any>;
  created_at: string;
  updated_at: string;
}

export interface OrganizationAnalytics {
  id: string;
  organization_id: string;
  metric_name: string;
  metric_value: number;
  metric_type: 'counter' | 'gauge' | 'histogram';
  time_period: 'hourly' | 'daily' | 'weekly' | 'monthly' | 'yearly';
  period_start: string;
  period_end: string;
  metadata: Record<string, any>;
  created_at: string;
}